<template>
	<NcAppContent>
		<div class="thinkfree-admin-settings">
			<!-- Title -->
			<h2>THINKFREE WEBOFFICE</h2><br><br>

			<!-- Server settings -->
			<h3>Server settings</h3>
			<p>
				Please register the domain of your installed <strong>Thinkfree WebOffice server</strong> here.<br />
			</p><br />

			<!-- Thinkfree WebOffice Server address -->
			<div class="form-group">
				<label for="documentServerUrl" style="font-size: 1.1em;"><strong>THINKFREE WEBOFFICE Server Address : </strong></label>
				<input
					id="documentServerUrl"
					type="text"
					v-model="serverAddress"
					placeholder="https://nextcloud.thinkfree.com/"
					size="40" />
			</div>
			<p>
				Make sure to replace <code>&lt;weboffice server address&gt;</code> in the example with the actual server address you are using.
			</p><br>

			<!-- Nextcloud AppKey For Thinkfree -->
			<div class="form-group">
				<label for="appKey" style="font-size: 1.1em;"><strong>App Password : </strong></label>
				<input id="appKey"
					v-model="appKey"
					type="password"
					placeholder="****************"
					size="40" />
			</div>
			<p>
				To generate a token, go to <strong>Settings &gt; Security &gt; Devices &amp; Sessions</strong>,<br />
				and issue an <strong>App Password</strong> to use as your authentication token.
			</p><br>

			<button class="button" @click="saveSettings">Save</button><br><br>
		</div>

	</NcAppContent>
</template>

<script>
import { showSuccess, showError } from '@nextcloud/dialogs'
import NcAppContent from '@nextcloud/vue/dist/Components/NcAppContent.js'

export default {
	name: 'App',
	components: {
		NcAppContent,
	},
	data() {
		return {
			showAdvanced: false,
			serverAddress: '',
			appKey: '',
		}
	},
	mounted() {
		this.loadSettings()
	},
	methods: {
		toggleAdvanced() {
			this.showAdvanced = !this.showAdvanced
		},
		async loadSettings() {
			try {
				const response = await fetch('/apps/thinkfree/settings/get', {
					headers: {
						'OCS-APIRequest': 'true',
						'X-Requested-With': 'XMLHttpRequest'
					}
				})
				if (!response.ok) {
					showError('Failed to load settings')
				}
				const data = await response.json()
				this.serverAddress = data.serverAddress || ''
				this.appKey = data.appKey || ''
			} catch (error) {
				showError('Failed to load settings')
				console.error('Error loading settings:', error)
			}
		},
		async saveSettings() {
			try {
				const requestToken = OC.requestToken
				const requestURL = '/index.php/apps/thinkfree/settings/set'
				const response = await fetch(requestURL, {
					method: 'POST',
					headers: {
						'Content-Type': 'application/json',
						'OCS-APIRequest': 'true',
						'X-Requested-With': 'XMLHttpRequest',
						requesttoken: requestToken
					},
					body: JSON.stringify({
						serverAddress: this.serverAddress,
						appKey: this.appKey,
					}),
				})
				if (response.ok) {
					showSuccess('saved!')
				} else {
					showError('Failed to save settings')
					console.error(`Fail to save settings, status: ${response.status}`)
				}
			} catch (error) {
				showError('Failed to save settings')
				console.error('Error saving settings:', error)
			}
		}
	}
}
</script>

